<script>
import axios from 'axios';
import Cookies from 'js-cookie';
export default {
    data() {
        return {
            arr: [],
            isActive: true,
            status1: "display: block",
            status2: "display: none",
            companyIcon: "",//企业图标
            companyName: "",//企业名称
            companyType: "",//企业性质
            companyField: "",//企业行业
            companyScale: "",//企业规模
            companyHome: "",//企业主页
            companyAddress: "",//企业地址
            companyText: "",//企业介绍
            followStatus: true,
            followButtonClass: "guanzhu"
        }
    },
    methods: {
        jumpToRecruitView(recruitId,companyId){
            window.location.href="/info?companyId="+companyId+"&recruitId="+recruitId
        },
        /* 申请职位方法 */
        apply(item) {
            let that = this;
            const cookieValue = Cookies.get('cookieUserId');
            axios({
                method: "post",
                url: '/api/apply',
                data: {
                    userId: cookieValue,
                    companyId: item.companyId,
                    recruitId: item.recruitId
                }
            })
                .then(response => {
                    let data = response.data
                    let code = data.code
                    let msg = data.msg
                    if (code === 300001) {
                        console.log(data)
                        item.userId = ""
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                    } else {
                        console.log(data)
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                    }
                })
        },
        /* 关注企业的方法 */
        follow() {
            console.log("click follow")
            let that = this;
            let companyId = this.$route.query.companyId;
            let cookieValue = Cookies.get('cookieUserId');
            axios({
                method: "get",
                url: '/api/follow/followCompany/' + companyId + '/' + cookieValue,
            })
                .then(response => {
                    let data = response.data
                    let code = data.code
                    let msg = data.msg
                    if (code === 100001) {
                        console.log(data)
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                        
                        that.followStatus = false
                    } else {
                        console.log(data)
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                    }
                })
        },
        unfollow() {
            console.log("click unfollow")
            let that = this
            let companyId = this.$route.query.companyId;
            let cookieValue = Cookies.get('cookieUserId');
            axios({
                method: "get",
                url: '/api/follow/unfollowCompany/' + companyId + '/' + cookieValue,
            })
                .then(response => {
                    let data = response.data
                    let code = data.code
                    let msg = data.msg
                    if (code === 100001) {
                        console.log(data)
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                        that.followStatus = true
                    } else {
                        console.log(data)
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                    }
                })
        }
    },
    mounted() {
        let companyId = this.$route.query.companyId
        console.log("companyId:",companyId)
        let that = this;
        const cookieValue = Cookies.get('cookieUserId');
        if (cookieValue == null || cookieValue == "") {
            this.$alert('请先登录', '警告', {
                confirmButtonText: '回去登陆',
                callback: action => {
                    window.location = "/"
                }
            });
            
        } else {
            /* 获取企业信息 */
            axios({
                method: 'GET',
                url: '/api/company/getCompanyInfo/' + companyId //你的后端路径
            })
                .then(response => {
                    let data = response.data
                    let code = data.code
                    let msg = data.msg
                    let info = data.object
                    if (code === 200001) { //判断你的请求是否成功
                        console.log(info)
                        that.companyIcon = info.companyIcon
                        that.companyName = info.companyName
                        that.companyType = info.companyType
                        that.companyField = info.companyField
                        that.companyScale = info.companyScale
                        that.companyHome = info.companyHome
                        that.companyAddress = info.companyAddress
                        that.companyText = info.companyText
                    } else {
                        this.$alert(msg, '提示', {
                            confirmButtonText: '确定'
                        });
                    }
                }, error => {
                    console.log('错误', error.message)
                    // alert(error.message)
                }),
                /* 获取企业下所有招聘信息 */
                axios({
                    method: 'GET',
                    url: '/api/company/getCompanyRecruit/' + companyId + '/' + cookieValue //你的后端路径
                })
                    .then(response => {
                        let data = response.data
                        let code = data.code
                        let msg = data.msg
                        let info = data.object
                        if (code === 200001) { //判断你的请求是否成功
                            console.log(info)
                            that.arr = info
                        } else {
                            this.$alert(msg, '提示', {
                                confirmButtonText: '确定'
                            });
                        }
                    }, error => {
                        console.log('错误', error.message)
                        // alert(error.message)
                    }),
                /* 判断企业是否被关注 */
                axios({
                    method: 'GET',
                    url: '/api/follow/checkFollowStatus/' + companyId + '/' + cookieValue
                })
                    .then(response => {
                        let data = response.data
                        let code = data.code
                        let msg = data.msg
                        let info = data.object
                        if (code === 100001) {
                            console.log(data.info)
                            that.followStatus = false
                        } else {
                            console.log(data)
                        }
                    }, error => {
                        console.log('错误', error.message)
                        // alert(error.message)
                    })
        }
    },
    computed: {
        formattedDate() {
            const date = new Date(this.recruitTime);
            return date.toLocaleString();
        },
    },
}
