<template>
    <div>
        <div class="jobtypeBox">
            <div class="DivLeft">
                <div class="DivCompany">
                    <div class="DivCompany_img"><img style="height: 150px;" :src="companyIcon" width="180" /></div>
                    <h1><a :href="'/info/company?companyId='+this.companyId" >{{ companyName }}</a><img src="/static/images/16.png" /></h1>
                    <div class="clear"></div>
                    <div class="DivLeft_2">
                        <p>行业：{{ companyField }}</p>
                        <p>性质：{{ companyType }}</p>
                        <p>规模：{{ companyScale }}</p>
                        <!-- <p>企业主页：<a :href="companyHome">{{ companyHome }}</a></p> -->
                        <p>详细地址：{{ companyAddress }}</p>
                        <p>{{ companyText }}</p>
                    </div>
                    <div class="clear"></div>
                </div>
            </div>
            <div class="DivRight">
                <div class="DivRight_2">
                    <div class="divJob_1">
                        <div class="divJob_l">
                            <div class="tit">
                                <h2>{{ recruitName }}</h2>
                                <div class="clear"></div>
                            </div>
                            <div class="bqbox">
                                <!-- <div v-for="(item, index)   in tagSplit" :key="index"> -->
                                    <!-- <label>{{ item }}</label> -->
                                    <el-tag
                                        v-for="(item, index) in tagSplit"
                                        :key="index"
                                        type="success"
                                        effect="light"
                                        style="margin-left: 6px;"
                                    >
                                        {{ item }}
                                    </el-tag>
                                <!-- </div> -->
                                <div class="clear"></div>
                            </div>
                            <div class="jobsub">工作地点： {{ recruitAddress }}</div>
                            <div class="jobsub">职位月薪： {{ salary }}</div>
                            <!-- <div class="jobsub">招聘人数： {{ recruitNumber }}人</div> -->
                            <div class="jobsub">最低学历： {{ recruitDegree }}</div>
                            <!-- <div class="jobsub">年龄要求： {{ recruitAge }}</div> -->
                            <div class="jobsub">工作经验： {{ recruitExp }}</div>
                            <div class="jobsub">招聘方式： {{ recruitType }}</div>
                            <div class="jobsub">刷新日期：{{ formattedDate }}</div>
                        </div>
                    </div>
                    <div class="clear"></div>
                    <div class="divJob_2">
                        <el-button type="warning" plain @click="applyForRecruit()">{{ applyStatus }}</el-button>
                    </div>
                    <div class="clear"></div>
                    <div class="divJob_3">
                        <b>岗位职责：</b><br />
                        <div class="con">
                            <div v-html="formatRecruitText(recruitTextDuty)"></div>
                        </div>
                        <!-- <b>岗位要求：</b><br />
                        <div class="con">
                            <div v-html="formatRecruitText(recruitTextNeed)"></div>
                        </div> -->
                    </div>
                    <div class="clear"></div>
                </div>
            </div>
        </div>
        <div class="clear"></div>
        <div class="footer">
        </div>
    </div>
</template>
<script>
import axios from 'axios';
import Cookies from 'js-cookie';
export default {
    data() {
        return {
            isLogin: false,

            username:"",

            companyIcon: "",//企业图标
            companyName: "",//企业名称
            companyType: "",//企业性质
            companyField: "",//企业行业
            companyScale: "",//企业规模
            companyHome: "",//企业主页
            companyAddress: "",//企业地址
            companyText: "",//企业介绍
            companyId:"",

            recruitId:'',
            recruitName: "",//职位名称
            recruitTag: "",//招聘标签
            recruitAddress: "",//职位地址
            salary: "",//薪水
            recruitNumber: "",//招聘人数
            recruitDegree: "",//招聘学历
            recruitAge: "",//招聘年龄
            recruitExp: "",//工作经验
            recruitType: "",//招聘方式
            recruitTime: "",//更新时间
            recruitTextDuty: "",//岗位职责
            recruitTextNeed: "",//岗位要求
            applyStatus: "立即申请",
            applyButtonClass: "divJob_2_1",
            cookieValue: "",


            startTime:null,
        }
    },
    methods: {
        applyForRecruit() {
            let that = this;
            const cookieValue = Cookies.get('cookieUserId');
            if (!this.isLogin) {
                this.$alert('未登陆,无法访问!', '警告', {
                    confirmButtonText: '回去登陆',
                    callback: action => {
                        window.location.href = "/"
                    }
                }); 
            } else {
                //用户申请职位逻辑写于此
                axios({
                method: "post",
                url: '/api/apply',
                data: {
                    userId: cookieValue,
                    companyId: that.companyId,
                    recruitId: that.recruitId
                }
            })
                .then(response => {
                    let data = response.data
                    let code = data.code
                    let msg = data.msg
                    let info = data.object
                    if (code === 300001) {
                        console.log(data)
                        that.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                        that.applyStatus = "已申请"
                        that.applyButtonClass = "divJob_2_0"
                    } else {
                        console.log(data)
                        that.$alert(msg, '提示', {
                            confirmButtonText: '确定',
                        });
                    }
                })

            }
        },
        formatRecruitText(text) {
            return text.replace(/\n/g, '<br>');
        },
    },
    mounted() {
        if (this.$cookie.get('cookieUserName') != null && this.$cookie.get('cookieUserName') != "") {
            this.isLogin = true
        }
        if(!this.isLogin){
            this.$alert('未登陆,无法访问!', '警告', {
                confirmButtonText: '回去登陆',
                callback: action => {
                    window.location.href = "/"
                }
            });      
        }
        this.startTime = new Date()
        console.log("用户进入时间:",this.startTime)
        this.username = this.$cookie.get("cookieUserName")
        let that = this;
        this.companyId = this.$route.query.companyId
        this.recruitId = this.$route.query.recruitId
        var userId = this.$route.query.userId
        this.cookieValue = Cookies.get('cookieUserId');
        console.log("companyId:"+this.companyId+";recruitId:"+this.recruitId+";cookieValue:"+this.cookieValue)
        axios({
            method: 'GET',
            url: '/api/company/getCompanyInfo/' + that.companyId //你的后端路径
        })
            .then(response => {
                let data = response.data
                let code = data.code
                let msg = data.msg
                let info = data.object
                if (code === 200001) { //判断你的请求是否成功
                    console.log("获取企业信息:",data)
                    // that.companyIcon = require('@/../public/static/images/' + info.companyIcon)
                    that.companyIcon = info.companyIcon
                    that.companyName = info.companyName
                    that.companyType = info.companyType
                    that.companyField = info.companyField
                    that.companyScale = info.companyScale
                    that.companyHome = info.companyHome
                    that.companyAddress = info.companyAddress
                    that.companyText = info.companyText
                } else {
                    that.$alert(msg, '警告', {
                        confirmButtonText: '确定'
                    });  
                }
            }, error => {
                console.log('错误', error.message)
                // alert(error.message)
            }),
        axios({
            method: 'GET',
            url: '/api/recruit/getRecruitInfo/' + that.companyId + '/' + that.recruitId + '/' + that.cookieValue
        })
            .then(response => {
                let data = response.data
                let code = data.code
                let msg = data.msg
                let info = data.object
                if (code === 200001) { //判断你的请求是否成功
                    console.log(data)
                    that.recruitName = info.recruitName
                    that.recruitTag = info.recruitTag
                    that.recruitAddress = info.recruitAddress
                    that.salary = info.recruitSalary+ "[参考工资]"
                    that.recruitNumber = info.recruitNumber
                    that.recruitDegree = info.recruitDegree
                    that.recruitAge = info.recruitAge
                    that.recruitExp = info.recruitExp
                    that.recruitType = info.recruitType
                    that.recruitTime = new Date(info.recruitTime)
                    that.recruitTextDuty = info.recruitTextDuty
                    that.recruitTextNeed = info.recruitTextNeed
                    if (info.userId != null) {
                        that.applyStatus = "已申请"
                        that.applyButtonClass = "divJob_2_0"
                    }
                } else {
                    that.$alert(msg, '提示', {
                        confirmButtonText: '确定',
                    });
                }
            }, error => {
                console.log('错误', error.message)
                // alert(error.message)
            })
        window.addEventListener("beforeunload", () => {
            // 获取结束时间
            var endTime = new Date();

            // 计算用户停留时间
            var duration = endTime - this.startTime;

            // 将用户停留时间发送到后端
            axios.post("/api/rec/analysis", {
                stayTime: duration,
                userId: this.$cookie.get('cookieUserId'),
                recruitId: this.recruitId
            });
        });
    },
    computed: {
        formattedDate() {
            const date = new Date(this.recruitTime);
            return date.toLocaleString();
        },
        tagSplit() {
            const tags = this.recruitTag.split("#")
            return tags;
        }
    },
}
</script>
